<template>
    <span class="go-back">
        <button @click="goBack"><ion-icon name="arrow-back-outline"></ion-icon>Back</button>
    </span>
</template>

<script>
    export default {
        methods: {
            goBack() {
                return this.$router.go(-1);
            }
        }
    }
</script>

<style scoped>
    .go-back button {
        display: flex;
        cursor: pointer;
        padding: .5em 1em;
        border-radius: 4px;
        color:  hsl(200, 15%, 8%);
        background-color: transparent;
        box-shadow:  0 .125rem .25rem rgba(0,0,0,.075);
        margin: 2em .5em;
        text-decoration: none;
        font-size: 14px;
    }
    .dark .go-back button{
        color:  hsl(0, 0%, 100%);
        background-color: hsl(207, 26%, 17%);
    }
    button {
        border: 0;
    }
    ion-icon {
        padding-right: .5em;
    }
</style>