<template>
    <header>
        <div class="content">
            <h1>Where in the world?</h1>
            <div class="mode" @click="updateDark()" v-if="isDark"><ion-icon name="sunny"></ion-icon> Light Mode</div>
            <div class="mode" @click="updateDark()" v-else><ion-icon name="moon"></ion-icon> Dark Mode</div>
        </div>
    </header>
</template>

<script>
    export default {
        data() {
            return {
                isDark: false
            }
        },
        methods: {
            updateDark() {
                this.isDark = !this.isDark;  
                this.$store.commit('setIsDark', this.isDark)              
            }
        }
    }
</script>
<style scoped lang="scss">
#countriespage {
    #nav {
        padding: 30px;
        a {
            font-weight: bold;
            color: #2c3e50;

            &.router-link-exact-active {
                color: #42b983;
            }
        }
    }
    header {
        padding: 2em 0;
        box-shadow: 0 .125rem .25rem rgba(0,0,0,.075);
        background-color: hsl(0, 0%, 100%);
        position: sticky;
        top: 50px;
        z-index: 100;
         .content{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            width: 80%;
            margin: 0 auto;
             h1 {
                margin: 0;
                padding: 0;
                font-size: 1.5em;
            }
        }
    }
    .mode {
        font-weight: 600;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

