<template>
    <div id="countriespage"  :class="{dark : isDark}">
        <Header />
        <div id="countryDisplay">
            <div class="back"><GoBack /></div>
            <div class="details">            
                <figure class="flag">
                    <img :src="country.flag" :alt="country.name">
                </figure>
                <div class="content">
                    <h1>{{ country.name }}</h1>
                    <ul>
                        <li><span class="header">Native Name: </span>{{country.nativeName}}</li>
                        <li><span class="header">Population: </span>{{country.population}}</li>   
                        <li><span class="header">Region: </span>{{country.region}}</li>
                        <li><span class="header">Sub Region: </span>{{country.subregion}}</li>
                        <li><span class="header">Capital: </span>{{country.capital}}</li>
                        <li><span class="header">Top Level Domain: </span><span v-for="(domain, index) in country.topLevelDomain" :key="domain">{{domain}} <span v-if="index != country.topLevelDomain.length - 1">, </span></span></li>
                        <li><span class="header">Currencies: </span><span v-for="(currency, index) in country.currencies" :key="currency.code">{{currency.name}} <span v-if="index != country.currencies.length -1">,</span></span></li>
                        <li><span class="header">Languages: </span><span v-for="(language, index) in country.languages" :key="language.iso639_1">{{language.name}} <span v-if="index != country.languages.length -1">,</span></span></li>
                    </ul>
                    <div class="borderCountries">
                        <span class="header">Border Countries: </span>
                        <div>
                            <router-link class="button" v-for="borderCountry in borderCountries" :key="borderCountry.alph3Code" :to="{name: 'Details', params: {slug: borderCountry.alpha3Code}}">{{borderCountry.name}}</router-link>
                        </div>         
                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import GoBack from '@/components/Countries/GoBack'
    import Header from '@/components/Countries/Header'
    export default {
        props: {
            slug: {
                type: String,
                required: true
            }
        },
       computed: {
           borderCountries() {             
               let bordercountries = [];
               if (this.country.borders) {
                this.country.borders.map(item => {
                    let found = this.countries.find(country => country.alpha3Code === item);
                    bordercountries.push(found)
                })
               }
               return bordercountries;
           },
            ...mapGetters({
                country: 'getCountry',
                countries: 'getCountries',
                isDark: 'isDark'
            })
            
        },
        methods: {
            fetchCountry() {
                this.$store.dispatch('fetchCountry', this.slug)
            },
            fetchCountries() {
                this.$store.dispatch('fetchCountries')
            }
        },
        created() {
            this.fetchCountry();
            this.fetchCountries();          
        },
        components: {
            GoBack,
            Header
        }
    }
</script>

<style scoped lang="scss"> 
#countriespage {
        margin: 0;
        padding: 0;
        font-family: 'Nunito Sans', sans-serif;
        font-size: 14px;
        color: hsl(200, 15%, 8%);
        min-height: 100vh;
        &.dark {
            background-color:hsl(209, 23%, 22%);
            color: hsl(0, 0%, 100%);
        }
    }
    #countryDisplay {
       min-height: calc(100vh - 80px);
       font-size: 16px;       
       width: 80%;
       margin: 0 auto;
    }
    .details {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
        @media only screen and (max-width: 63em) {
            flex-direction: column;
        }
    }
    figure {
        width: 50%;
        margin: 0;
        padding: 0;
        @media only screen and (max-width: 63em) {
            width: 100%;
            text-align: center;
        }
    }
    figure img {
        width: 100%;
        @media only screen and (max-width: 63em) {
            width: auto;            
            max-height: 200px;
            margin: 0 auto;
        }
    }
    .content {
        width: 50%;
        text-align: left;
        padding: 1em;
        margin: 0 1em;
         @media only screen and (max-width: 63em) {
            width: 100%;
            flex-direction: column;
            margin: 0;
        }
    }
    ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        column-count: 2;
         @media only screen and (max-width: 39em) {
            column-count: 1;
        }
    }
    ul li {
        margin: .5em 0;
        font-size: .85em;
    }
    ul li .header {
        font-weight: 800;
    }
    .borderCountries {
        margin-top: 2em;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
         @media only screen and (max-width: 39em) {
            flex-direction: column;
        }
    }
    .borderCountries > div {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .borderCountries .header {
        font-weight: 600;
        width: 50%;
    }
    a.button {
        padding: .25em .5em;
        border-radius: 4px;
        color:  hsl(200, 15%, 8%);
        background-color: transparent;
        box-shadow:  0 .125rem .25rem rgba(0,0,0,.075);
        margin: 0 .25em .5em .25em;
        text-decoration: none;
    }
    .dark a.button {
        color:  hsl(0, 0%, 100%);
        background-color: hsl(207, 26%, 17%);
    }
</style>